import { Component, Element, Event, EventEmitter, FunctionalComponent, h, Host, Listen, Method, Prop, State } from '@stencil/core';
import { DropdownMode, DropdownOpenMode, DropdownPosition, DropdownSize } from '../../../interfaces/components/basic/dropdown/dropdown';
import { IPosition } from '../../../interfaces/data/position';
import { EventManager } from '../../../utils/actions/eventManager';

@Component({
  tag: 'rwc-dropdown',
  styleUrl: './dropdown.scss',
  shadow: true
})
export class RwcDropdown {
  /**
   * Event manager used to manage component's event subscriptions.
   */
  private eventManager: EventManager = new EventManager();

  /**
   * Reference to the parent of the dropdown.
   */
  private parent: HTMLElement;

  /**
   * Reference to the wrapper of the dropdown.
   */
  private wrapperElement: HTMLDivElement;

  /**
   * Reference to the container of the dropdown.
   */
  private containerElement: HTMLDivElement;

  /**
   * Reference to the host element of the component.
   */
  @Element() host: HTMLRwcDropdownElement;

  /**
   * The open mode of the dropdown.
   */
  @Prop({ reflect: true }) openMode: DropdownOpenMode = 'manual';

  /**
   * The position of the dropdown from the parent.
   */
  @Prop({ reflect: true }) position: DropdownPosition = 'bottom';

  /**
   * The sizing of the dropdown.
   */
  @Prop({ reflect: true }) size: DropdownSize = 'parent';

  /**
   * Determines the rendering behaviour of the dropdown.
   */
  @Prop({ reflect: true }) mode: DropdownMode = 'fixed';

  /**
   * Determines if the dropdown is open.
   */
  @State() isOpen: boolean = false;

  /**
   * Event fired when the dropdown opening is triggered.
   */
  @Event({ eventName: 'dropdownOpen' }) dropdownOpenEvent: EventEmitter<void>;
  
  /**
   * Event fired when the dropdown closing is triggered
   */
  @Event({ eventName: 'dropdownClose' }) dropdownCloseEvent: EventEmitter<void>;

  /**
   * Stencil event called the first time the component is to be initialized.
   */
  componentWillLoad(): void {
    this.parent = this.host.parentElement;
    this.init();
  }

  /**
   * Stencil event called when the component is fully loaded for the first time.
   */
  componentDidLoad(): void {
    this.wrapperElement.style.height = `0px`;
  }

  /**
   * Fires when the component is about to be destroyed.
   */
  disconnectedCallback(): void {
    this.eventManager.clearEventListeners();
  }

  /**
   * Initialize the component.
   */
  @Method()
  async init(): Promise<void> {
    this.eventManager.clearEventListeners();
    this.initHoverEvents();
    this.initClickEvents();
  }

  /**
   * Handles the scroll event.
   */
  @Listen('scroll', { target: 'window' })
  onWindowScroll(): void {
    if (this.mode === 'fixed') {
      this.calculateDropdownOffset();
    }
  }

  /**
   * Handles the window resize event.
   */
  @Listen('resize', { target: 'window' })
  onWindowResize(): void {
    if (this.isOpen) {
      this.setState(false);
    }
  }

  /**
   * Closes the dropdown on click outside the dropdown wrapper.
   */
  @Listen('click', { target: 'document' })
  async onDocumentClick(event: MouseEvent): Promise<void> {
    if (this.isOpen && !event.composedPath().includes(this.wrapperElement)) {
      await new Promise(requestAnimationFrame);
      this.setState(false);
    }
  }

  /**
   * Initialize the hover event listeners.
   */
  private initHoverEvents(): void {
    if (this.openMode === 'hover') {
      this.eventManager.registerEventListener(this.parent, 'mouseenter', () => this.setState(true));
      this.eventManager.registerEventListener(this.parent, 'mouseleave', () => this.setState(false));
    }
  }

  /**
   * Initialize the click event listeners.
   */
  private initClickEvents(): void {
    if (this.openMode === 'toggle') {
      this.eventManager.registerEventListener(this.parent, 'click', async () => {
        if (!this.isOpen) {
          await new Promise(requestAnimationFrame);
          this.setState(true);
        }
      });
    }
  }

  /**
   * Set state of the dropdown.
   */
  @Method()
  async setState(value: boolean): Promise<void> {
    this.handleStateChange(value);
  }

  /**
   * Handle the state change of the dropdown.
   */
  async handleStateChange(value: boolean): Promise<void> {
    this.isOpen = value;
    
    if (this.isOpen) {
      this.dropdownOpenEvent.emit();
    } else {
      this.dropdownCloseEvent.emit();
    }
    
    await new Promise(requestAnimationFrame);
    this.calculateDropdownSize();
    this.calculateDropdownOffset();
  }

  /**
   * Calculate the position of the dropdown.
   */
  async calculateDropdownOffset(): Promise<void> {
    if (this.wrapperElement && this.parent) {
      let offset = this.getStartingOffset();
      offset = this.calculateSideOffset(offset);
      offset = this.fixOverflow(offset);
      
      this.wrapperElement.style.left = this.isOpen ? `${ offset.x }px` : '';
      this.wrapperElement.style.top = `${ offset.y }px`;
    }
  }

  /**
   * Get the starting offset of the dropdown.\
   * Returns zero for non fixed modes.
   */
  private getStartingOffset(): IPosition {
    if (this.mode === 'fixed') {
      return {
        x: this.parent.getBoundingClientRect().left,
        y: this.parent.getBoundingClientRect().top
      }
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }

  /**
   * Get the offset for the side position of the dropdown.
   */
  private calculateSideOffset({ x, y }: IPosition): IPosition {
    if (this.position === 'side') {
      x += this.parent.offsetWidth;
      if (this.mode === 'absolute') {
        y = - this.parent.offsetHeight;
      }
    } else if (this.mode === 'fixed') {
      y += this.parent.offsetHeight;
    }

    return {
      x: x,
      y: y
    }
  }

  /**
   * Calculate the right overflow and position the dropdown to fit the screen.
   */
  private fixOverflow({ x, y }: IPosition): IPosition {
    const rightEdgePosition = this.mode === 'absolute' ?
      this.parent.getBoundingClientRect().left + x + this.wrapperElement.offsetWidth :
      x + this.wrapperElement.offsetWidth;

    if (rightEdgePosition > window.innerWidth) {
      x -= rightEdgePosition - window.innerWidth;
    }

    return {
      x: x,
      y: y
    }
  }

  /**
   * Calculate the size of the dropdown if set to parent size.
   */
  private async calculateDropdownSize(): Promise<void> {
    this.wrapperElement.style.height = `${this.isOpen ? this.containerElement.offsetHeight : 0}px`;
    if (this.mode === 'fixed' && this.size === 'parent') {
      this.wrapperElement.style.width = `${ this.parent.offsetWidth }px`;
    } else {
      this.wrapperElement.style.width = '';
    }
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <div
          ref={ (element: HTMLDivElement) => this.wrapperElement = element }
          class={ {
            wrapper: true,
            open: this.isOpen,
            [`mode-${ this.mode }`]: true,
            [`size-${ this.size }`]: true
          } }>
          <div
            ref={ (element: HTMLDivElement) => this.containerElement = element }
            class="container"
          >
            <div class="content">
              <slot></slot>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
